<script>
import VueRecaptcha from 'vue-recaptcha';
import VanillaModal from 'vanilla-modal';

window.modal  = VanillaModal;
var verify    = false;

export default {
  name: 'Main',

  data(){
    return {
          ok : false,
          res : true,
          resx: false,
          finalres : true,
          showErrorCapcha : false,
          modal : false,

          form: {
            terms : '',
            nameap: "",
            dniap: "",
            name: "",
            dni: "",
            phone: "",
            email: "",
            venue: "",
            grade: "",
            comment: "",
            method_contact: ""
          },

          sedes : {
            'Breña' : [
              'Secundaria'
            ],

              'Arequipa' : [
                'Primaria',
                'Secundaria'
              ],

              'Chiclayo' : [
                'Primaria',
                'Secundaria'
              ],

              'Chanchamayo' : [
                'Primaria',
                'Secundaria'
              ],

              'Huancayo' : [
                'Secundaria'
              ],


            'Piura' : [
              'Primaria',
              'Secundaria'
            ],

            'Trujillo' : [
              'Primaria',
              'Secundaria'
            ],


            'Callao' : [
              'Secundaria'
            ],

            'Chorrillos' : [
              'Secundaria',
              'Primaria'
            ],

            'Comas' : [
              'Secundaria',
              'Primaria'
            ],

            'Los Olivos' : [
              'Secundaria',
              'Primaria',
              'Inicial'
            ],

            'San Miguel (Maranga)' : [
              'Secundaria'
            ],

            'Santa Beatriz (Roma)' : [
              'Secundaria'
            ],

            'Jesús María (Salaverry)' : [
              'Secundaria',
              'Primaria'
            ],

            'Pro' : [
              'Secundaria',
              'Primaria'
            ],

            'Surco' : [
              'Primaria',
              'Secundaria'
            ],

            'San Isidro' :[
              'Secundaria'
            ],

            'Villa María del Triunfo': [
              'Secundaria',
              'Primaria',
              'Inicial'
            ],

            'Villa El Salvador' : [
              'Secundaria',
              'Primaria',
              'Inicial'
            ],

            'La Molina' : [
              'Secundaria',
              'Primaria'
            ],

            'Santa Anita' : [
              'Secundaria',
              'Primaria'
            ],

            'Ate (Salamanca)' : [
              'Secundaria',
              'Primaria',
              'Inicial'
            ],

            'San Juan de Lurigancho (Wiesse)' : [
              'Secundaria',
              'Primaria'
            ],

            'San Juan de Lurigancho' : [
              'Secundaria',
              'Primaria'
            ]
          }
    }
  },

  components: {
    VueRecaptcha
  },

  methods : {
    was(){
      const select = document.querySelector('#grade');
      const x = this.sedes[this.form.venue];

      while (select.options.length) select.remove(0);
      let v = document.createElement('option');
      v.text = 'Grado de interés';
      v.selected = true;
      v.disabled = true;
      v.hidden = true;
      select.add(v);

      this.form.grade  = 'Grado de interés';

      x.forEach(function(a,i){
        let s = document.createElement('option');
        s.text = a;
        s.value = a;
        select.add(s);
      });

    },

    setContact(method){
      this.form.method_contact = method;
      this.modal.close();
      this.ok = true;
      this.onSend();
    },

    onVerify(){
      verify = true;
      this.showErrorCapcha = false;

    },

    onSubmit() {
      this.$validator.validateAll().then((result) => {

        gtag('event', 'conversion', {
          'allow_custom_scripts': true,
          'send_to': 'DC-8953419/trilc0/lpg_a0+standard'
        });

        if (result) {
          if(this.form.terms){
            if(verify){
              this.modal = new VanillaModal();
              var modal_open = ['#modal-1'];
              this.modal.open(modal_open[0]);
            }else{
              this.showErrorCapcha = true;
            }
          }

        } else {

        }
      }).catch((e) => {
        console.log(e)
        alert('Error');
      });

    },

    onSend(){
      console.log(this.form);

      this.$http.post('api.php', JSON.stringify(this.form)).then(function(r){
        this.resx = true;

        if(r.data.status){
          // Shoot pixel!
          this.res = false;
          //
          // gtag('event', 'conversion', {
          //   'allow_custom_scripts': true,
          //   'send_to': 'DC-8953419/trilc00/reg_a0+standard'
          // });

//          console.log("Shoot pixel");

          location.href= "./gracias.php?n="+ this.form.nameap;

        }else{
          this.finalres = false;
          console.log("User exits");
        }

      }),function(r){
        console.log(r.data);
      }

    },

    isNumber: function(evt) {
      var re = new RegExp( /^\d+$/);
      if(re.test(evt.key))
        return true;
      else
        evt.stopPropagation();
        evt.preventDefault();
        return false;
